@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap");

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	display: flex;
	height: 100vh;
	background-color: #0b2434;
	justify-content: center;
	align-items: center;
}

#root {
	background-color: lightgrey;
	width: 90vw;
	height: 90vh;
	border-radius: 5px;
	padding: 20px;
}

main {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
}

.dice-container {
	margin-top: 80px;
	display: grid;
	grid-template-columns: repeat(5, auto);
	gap: 20px;
}

.dice {
	font-size: 35px;
}

.dice-face {
	cursor: pointer;
	display: flex;
	justify-content: center;
	box-shadow: darkgray 0 4px 5px, darkgray 1px 4px 5px, darkgray -1px 4px 5px;
	align-items: center;
	border-radius: 10px;
	text-align: center;
	height: min(75px, 12vw);
	width: min(75px, 12vw);
}

.btn {
	background-color: blueviolet;
	border: none;
	height: 60px;
	border-radius: 5px;
	margin-top: 80px;
	width: 160px;
	font-size: x-large;
	font-weight: bold;
	color: #f5f5f5;
	text-decoration: none;
	box-shadow: darkgray 0 4px 20px;
	cursor: pointer;
}

header {
	text-align: center;
	margin-top: 50px;
}

.introduction {
	font-family: "inter";
	font-size: x-large;
	margin: 0 85px;
	font-weight: 500;
}

.title {
	font-size: 40px;
	font-weight: bold;
	margin-bottom: 10px;
}

.btn:active {
	transform: scale(0.95);
}

.btn:focus {
	outline: none;
}
